import {classes} from 'html-components';
const {Img} = classes;
const _Image = window.Image;

export class Image extends Img {
  init() {
    super.init();
    this.$$options.src = '';
    this.$$options.srcset = '';
  }

  onLoad() {
    const srcset = _.get(this.imageLoader, 'srcset', '');
    const src = _.get(this.imageLoader, 'src', '');
    srcset && (this.$$element.srcset = srcset);
    src && (this.$$element.src = src);
  }

  options(vNode) {
    super.options(vNode);
    if (!this.imageLoader) {
      this.imageLoader = this.imageLoader || new _Image();
      this.imageLoader.onload = this.onLoad.bind(this);
    }

    const src = _.invoke(this, 'src');
    const srcset = _.invoke(this, 'srcset');
    const loader = this.imageLoader;

    _.isString(src) && !_.isEqual(loader.src, src) && (loader.src = src);
    _.isString(srcset) && !_.isEqual(loader.srcset, srcset) && (loader.srcset = srcset);
  }

  props(vNode) {
    super.props(vNode);
    const src = _.get(this, '$$element.src');
    const srcset = _.get(this, '$$element.srcset');
    src && (this.$$props.src = src);
    srcset && (this.$$props.srcset = srcset);
  }
}

export default Image.register();



