import elements, {classes} from 'html-components';
const {img} = elements;
const {A} = classes;

export class Logo extends A {
  init() {
    super.init();
    this.$$options.imageUrl = '';
  }

  compose() {
    this.$$children = img({src: this.imageUrl()});
  }
}

export default Logo.register();



