import Ampalytics from 'Ampalytics';
let tracker;

const plugin = new Ampalytics.Plugin({
  name: 'ampush',
  url: '//files.ampush.io/tracker.js',
  key: 'ampt',
  postInit() {
    tracker =  window.ampt;
    this.ampt.init(plugin.id);
  },
  events: {
    init(info) { tracker.pageView(info.queryParams); }
  }
});

export default plugin;



