import {Button} from '../button/button';
import styles from './facebook-button.scss';
import elements from 'html-components';
const {i, div} = elements;

class FacebookButton extends Button {
  init() {
    super.init();
    this.$$classes.push(styles.button);
    this.$$options.onclick = _.noop;
    this.$$props.onclick = this.login.bind(this);
  }

  $$FB() {
    return this.$$global.analytics.sdk.facebook;
  }

  login(event) {
    this.$$global.analytics.ampt.click('fblogin');
    event.stopPropagation();
    this.loggedIn === 'connected' ? this.onLogin({}) : this.$$FB().login(response => this.onLogin(response), {scope: 'public_profile,email'});
  }

  onLogin({status = this.loggedIn}) {
    const onClick = this.onclick();
    /* eslint-disable-next-line camelcase */
    status === 'connected' ? this.$$FB().api('/me', {fields: 'email, first_name, last_name'}, onClick) : onClick({});
  }

  compose() {
    this.$$children = [
      div({classes: [styles.icon]}, i({classes: ['fa', 'fa-lg', 'fa-facebook', styles.facebookLogo]})),
      div({classes: [styles.text]}, 'Continue with Facebook')
    ];
  }

  postRender() {
    setTimeout(() => this.$$FB() && this.$$FB().getLoginStatus(({status = ''}) => { this.loggedIn = status;}));
  }
}

export default FacebookButton.register();



