import styles from './menu.scss';
import {classes} from 'html-components';

const {Menu: DefaultMenu} = classes;

export class Menu extends DefaultMenu {
  init(vNode) {
    super.init(vNode);
    this.$$classes.push(styles.menu);
    this.$$options.toggle = '';
    this.on('finished', this.onFinished.bind(this));
  }

  onFinished() {
    const toggle = this.$$element.querySelector(this.toggle());
    console.log(toggle.onclick);
  }
  //
  // onClick(prevOnclick, ...args) {
  //   prevOnclick.bind(this)(...args);
  // }
}

export default Menu.register();



