import {
  getCookieObject,
  getQueryParamsObject,
  getQueryStr,
  deserialize
} from './scripts/helper-functions';

import {
  ACCESS_TOKEN,
  API_URL,
  DEFAULT_UTM_CAMPAIGN_ID,
  DEFAULT_UTM_TOKEN,
  HTML_METHODS,
  UTM,
  WHITELISTED_UTM,
  QUERY_STRING
} from './scripts/constants';

export default class Ampalytics {
  constructor(partner) {
    _.assign(this, {
      partner,
      medium: _.get(getQueryParamsObject(QUERY_STRING), UTM.medium, 'other').toLowerCase(),
      plugins: {},
      ampId: DEFAULT_UTM_TOKEN
    });
    this.generateAmpId();
    this.activate();
  }

  activate() {
    this.ampId === DEFAULT_UTM_TOKEN && this.generateCookie();
    if (!this.active) {
      _.each(_.get(this, 'partner.plugins'), (id, name) => {
        _.set(this, ['plugins', name], Ampalytics.plugins[name].init(id));
      });
      this.track('init', {queryParams: this.queryParams});
      this.active = true;
    }
  }

  generateAmpId() {
    if (this.ampId !== DEFAULT_UTM_TOKEN) {
      return new Promise(res => res(this.ampId));
    }
    const queryParams = getQueryParamsObject(window.location.search);
    queryParams[UTM.campaign] = queryParams[UTM.campaign] || DEFAULT_UTM_CAMPAIGN_ID;
    queryParams.client = this.partner.name.toLowerCase();
    queryParams['access_token'] = ACCESS_TOKEN;
    this.queryParams = queryParams;
    const url = `${API_URL}utmparams/generate?${getQueryStr(queryParams)}`;
    const method = HTML_METHODS.post;
    const handleResponse = ({ampId}) => {
      this.ampId = ampId;
      this.generateCookie();
      return this.ampId;
    };
    return m.request(url, {data: new FormData(), method, deserialize})
      .then(({amp_id: ampId, status}) => ({ampId: status ? ampId : DEFAULT_UTM_TOKEN}), err => err)
      .then(handleResponse, handleResponse);
  }

  generateCookie() {
    if (this.cookie) {
      return this.cookie;
    }
    this.queryParams = this.queryParams || getQueryParamsObject(window.location.search);
    const cookieParams = JSON.parse(getCookieObject(document.cookie)[UTM.params] || '{}');
    this.campaignId = this.queryParams[UTM.campaign] || cookieParams[UTM.id] || DEFAULT_UTM_CAMPAIGN_ID;
    this.queryParams[UTM.source] = 'ampush';
    this.queryParams[UTM.medium] = this.queryParams[UTM.medium] || 'other';
    cookieParams[UTM.id] = undefined;
    this.queryParams[UTM.campaign] = this.ampId;
    _.assignWith(this.queryParams, cookieParams, (origin, source) => origin ? origin : source);
    const cookie = _.mapValues(this.queryParams, (param, key) => WHITELISTED_UTM[key] && param);
    cookie[UTM.id] = this.campaignId;
    document.cookie = `${UTM.params}=${JSON.stringify(cookie)};`;
    this.cookie = cookie;
    this.fixReferrer();
    return this.cookie;
  }

  getQuery() {
    return `${getQueryStr(_.mapValues(this.queryParams, (param, key) => WHITELISTED_UTM[key] && param))}`;
  }

  fixReferrer() {
    const  {medium, token, partner} = this;
    const path = `${window.location.origin}${window.location.pathname}?${this.getQuery()}`;
    history.pushState({medium, token, client: partner.name, path}, partner.title, path);
  }

  clearReferrer() {
    const path = `${window.location.origin}${window.location.pathname}`;
    history.replaceState({}, document.title, path);
  }

  track(trackEvent, trackInfo) {
    _.each(this.plugins, plugin => plugin.track(trackEvent, trackInfo));
  }
}

Ampalytics.Plugin = class Plugin {
  constructor(config) {
    this.events = {};
    _.assign(this, config);
    _.set(Ampalytics, ['plugins', config.name], this);
  }

  init(id) {
    this.id = id;
    _.invoke(this, 'preInit');
    $script(this.url, this.name, _.invoke(this, 'postInit'));
    return this;
  }

  track($event, info) {
    $script.ready(this.name, () => _.invoke(this, ['events', $event], info));
  }
};



