import {classes} from 'html-components';
import styles from './error-message.scss';

const {Span} = classes;

export class ErrorMessage extends Span {
  init() {
    super.init();
    this.$$options.for = null;
    this.$$classes.push(styles.errorMessage);
  }
  children(vNode) {
    super.children(vNode);
    this.input ? this.$$children.push(this.input.errorMessage) : this.$$children = [''];
  }

  update(message) {
    message = _.isObject(message) ? m(message) : message || '';
    m.render(this.$$element, message);
  }

  postRender() {
    this.input = _.get(this.$$components, ['FormInput', this.for()]);
    this.input && (this.input.__errorMessage__ = this);
  }
}

export default ErrorMessage.register();



