import {classes} from 'html-components';
import {} from 'scripts/helper-functions';
import styles from './form-input.scss';
const requiredError = 'This is a required field';

const {Input} = classes;

export class FormInput extends Input {
  init() {
    super.init();
    this.$$options.formModel = {};
    this.$$options.validation = _.noop;
    this.$$options.validateOn = 'blur';
    this.$$options.error = 'not valid';
    this.$$options.validateDebounce = 0;
    this.$$classes.push(styles.formInput);
    this.on('change', this.onchange.bind(this));
  }

  compose() {
    super.compose();
    const validate = _.debounce(this.validate.bind(this), this.validateDebounce());
    this.on(this.validateOn(), validate);
  }

  onchange(e, val) {
    e && (e.redraw = false);
    const model = this.formModel();
    model[this.name] = _.trim(val || this.$$element.value);
    this.$$element.value = model[this.name];
    this.validate(e);
  }

  setValue(val) {
    this.onchange(null, val);
  }


  setError(message = '') {
    this.errorMessage = message;
    this.formModel()[`${this.name}error`] = message;
    this.__errorMessage__ && this.__errorMessage__.update(message);
  }

  validate(e, disableAsync) {
    return !this.getValidity(this.validation()(e, this.formModel()[this.name], this), disableAsync);
  }

  getValidity(errorMessage = '', disableAsync) {
    if (errorMessage.constructor.name === 'Promise') {
      errorMessage.then(data => this.getValidity(data));
      errorMessage = disableAsync && this.valid ? '' : '   ';
    }
    this.config.required && !this.formModel()[this.name] && (errorMessage = requiredError);
    errorMessage = errorMessage ? errorMessage : !this.$$element.checkValidity() && this.error();
    this.setError(errorMessage);
    return errorMessage;
  }

  props(vNode) {
    super.props(vNode);
    this.name = this.config.name;
    this.$$props.value = this.formModel()[this.config.name];
    this.errorMessage = this.formModel()[`${this.name}error`];
  }
}

export default FormInput.register();



