import Ampalytics from 'Ampalytics';
let tracker;

const plugin = new Ampalytics.Plugin({
  name: 'bing',
  url: '//cdn.taboola.com/libtrc/${id}/tfa.js',
  key: '_tfa',
  preInit() {
    window._tfa = window._tfa || [];
  },
  postInit() {
    tracker =  window._tfa;
    tracker.push({
      notify: 'mark',
      type: 'retargeting'
    });
  }
});

export default plugin;



