
export const getCookieObject = cookie => _.chain(cookie.split(';'))
  .map(val => _.trim(val).split('='))
  .keyBy(0)
  .mapValues(([, val]) => val)
  .value();

export const getQueryStr = objParams => _.chain(objParams)
  .toPairs()
  .filter(([, val]) => !!val)
  .map(val => val.join('='))
  .join('&')
  .value();

export const getQueryParamsObject = query => _.chain(query)
  .split('?')
  .tail()
  .split('&')
  .compact()
  .map(val => val.split('='))
  .keyBy(0)
  .mapValues(([, val]) => val && decodeURIComponent(val.replace(/\+/g, ' ')))
  .value();


export const deserialize = val => {
  let returnVal;
  try {
    returnVal = JSON.parse(val);
  } catch (e) {} //eslint-disable-line no-empty
  return returnVal || {error: val};
};

export const generateID = () => Math.round(Math.random() * 10000000000000000);

export const getOptimalImage = images => _(images)
.sortBy(image => Math.abs(image.height / image.width - window.innerHeight / window.innerWidth))
.filter(image => window.innerWidth < image.width)
.head() || images[images.length - 1];

export const intercept = call => {
  const promise = new Promise(resolve => {
    const original = window.XMLHttpRequest;
    class t {
      constructor() {
        this.request = new original();
      }
      open(...args) {
        this.request.open(...args);
      }
      send(...args) {
        this.request.send(...args);
        const interval = setInterval(() => {
          if (this.request.response) {
            resolve(JSON.parse(this.request.response));
            clearInterval(interval);
          }
        }, 100);
      }
    }
    window.XMLHttpRequest = t;
    call();
    window.XMLHttpRequest = original;
  });
  return promise;
};

const __cache = {};
export const getEnv = (BREAKPOINTS = __cache.BREAKPOINTS, ORIENTATIONS = __cache.ORIENTATIONS, isMobile = __cache.isMobile) => {
  _.assign(__cache, {BREAKPOINTS, ORIENTATIONS, isMobile});
  const myEnv = _.mapValues(BREAKPOINTS, size => window.innerWidth > size);
  let rotation = Math.abs(_.get(window, 'screen.orientation.angle', window.orientation) - (isMobile() ? 90 : 0));
  !isMobile() && (rotation = window.innerWidth > window.innerHeight ? 0 : 90);
  myEnv.orientation = ORIENTATIONS[rotation];
  myEnv.isLandscape = myEnv.orientation === 'landscape';
  return myEnv;
};

export const hide = className => {
  document.getElementsByClassName(className)[0].style.display = 'none';
};

export const show = (className, visible = 'block') => {
  document.getElementsByClassName(className)[0].style.display = visible;
};

export const addComponent  = (NewComponent, name = NewComponent.constructor.name) => {
  // eslint-disable-next-line no-console
  window.$$draconis.__Components[name] && console.warn('A component already has that name');
  window.$$draconis.__Components[name] = NewComponent;
  window.$$draconis.__components[name] = (...args) => new NewComponent(...args);
};



