import {Component} from './core/component.class';
import store from './core/component.store';
import Ampalytics from './core/Ampalytics';
import './analytics-plugins/ampush.js'; import './analytics-plugins/bing.js'; import './analytics-plugins/facebook-sdk.js'; import './analytics-plugins/facebook.js'; import './analytics-plugins/google.js'; import './analytics-plugins/taboola.js'; import './analytics-plugins/yahoo.js';
import './components/button/button.js'; import './components/column/column.js'; import './components/error-message/error-message.js'; import './components/facebook-button/facebook-button.js'; import './components/form-input/form-input.js'; import './components/image/image.js'; import './components/link/link.js'; import './components/logo/logo.js'; import './components/menu/menu.js'; import './components/row/row.js';
const {classes, components, state} = store;

_.each(document.querySelectorAll('[draconis]'), tag => {
  const component = _.get(components, tag.getAttribute('draconis'));
  const children = tag.innerHTML;
  const config = {wait: !!children};
  if (component) {
    _.transform(tag.attributes, (result, attr) => {
      const key = attr.name === 'class' ? 'classes' : attr.name;
      const val = attr.name === 'class' ? attr.nodeValue.split(' ') : attr.nodeValue;
      result[key] = val;
    }, config);

    const fakeParent = document.createElement('span');
    m.mount(fakeParent, component(config));
    const newTag = fakeParent.firstChild;
    newTag.innerHTML = children;
    tag.parentNode.replaceChild(newTag, tag);
    newTag.dispatchEvent(new Event('finished'));
  }
});

setTimeout(() => { window.Draconis.analytics = new Ampalytics(window.__PARTNER_ENV__); });
export {
  Component,
  classes,
  components,
  state,
  Ampalytics
};



